'use client';

import { useEffect, useMemo, useState } from 'react';
import Link from 'next/link';
import { searchJobs, logCall, markCalled, type Job } from '@/lib/api';

type Row = Partial<Job> & { tag: string; callAttempts?: number; lastCallAt?: string; callNotes?: string; };

export default function RecallsReportPage() {
  const [rows, setRows] = useState<Row[]>([]);
  const [loading, setLoading] = useState(false);
  const [q, setQ] = useState('');
  const [draft, setDraft] = useState<Record<string, { notes: string; busy?: boolean; msg?: string }>>({});
  const [err, setErr] = useState('');

  const load = async () => {
    setLoading(true);
    setErr('');
    try {
      const res = await searchJobs('@report');
      if (!res?.ok) throw new Error(res?.error || 'Failed to load');
      const all: Row[] = res.rows || [];
      const called = all.filter(r => {
        const s = (r.status || '').toLowerCase();
        const c = (r.capingStatus || '').toLowerCase();
        const w = (r.webbsStatus || '').toLowerCase();
        const picked =
          s.includes('picked up') || c.includes('picked up') || w.includes('picked up');
        const isCalled = s.includes('called') || c.includes('called') || w.includes('called');
        return isCalled && !picked;
      });
      setRows(called);
      const seed: Record<string, { notes: string }> = {};
      called.forEach(r => (seed[r.tag] = { notes: '' }));
      setDraft(prev => ({ ...seed, ...prev }));
    } catch (e: any) {
      setErr((e?.message || String(e)));
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => { load(); }, []);

  const view = useMemo(() => {
    if (!q.trim()) return rows;
    const needle = q.trim().toLowerCase();
    return rows.filter(r =>
      (r.tag || '').toLowerCase().includes(needle) ||
      (r.customer || '').toLowerCase().includes(needle) ||
      (r.phone || '').toLowerCase().includes(needle)
    );
  }, [rows, q]);

  const log = async (tag: string) => {
    const d = draft[tag] || { notes: '' };
    setDraft(prev => ({ ...prev, [tag]: { ...d, busy: true, msg: '' } }));
    try {
      const out = await logCall({ tag, notes: d.notes || '' });
      if (!out?.ok) throw new Error(out?.error || 'Failed to log');
      setDraft(prev => ({ ...prev, [tag]: { notes: '', busy: false, msg: 'Logged ✓' } }));
      await load();
      setTimeout(() => setDraft(prev => ({ ...prev, [tag]: { ...(prev[tag]||{notes:''}), msg: '' } })), 1200);
    } catch (e: any) {
      setDraft(prev => ({ ...prev, [tag]: { ...(prev[tag]||{notes:''}), busy:false, msg: e?.message || String(e) } }));
    }
  };

  const mark = async (tag: string) => {
    const d = draft[tag] || { notes: '' };
    setDraft(prev => ({ ...prev, [tag]: { ...d, busy: true, msg: '' } }));
    try {
      const out = await markCalled({ tag });
      if (!out?.ok) throw new Error(out?.error || 'Failed to mark');
      setDraft(prev => ({ ...prev, [tag]: { ...(prev[tag]||{notes:''}), busy:false, msg:'Marked ✓' } }));
      await load();
      setTimeout(() => setDraft(prev => ({ ...prev, [tag]: { ...(prev[tag]||{notes:''}), msg: '' } })), 1200);
    } catch (e: any) {
      setDraft(prev => ({ ...prev, [tag]: { ...(prev[tag]||{notes:''}), busy:false, msg: e?.message || String(e) } }));
    }
  };

  return (
    <div className="light-page">
      <div className="head">
        <h1>Recalls</h1>
        <div className="tools">
          <input
            className="search"
            placeholder="Search by tag, name, phone…"
            value={q}
            onChange={(e) => setQ(e.target.value)}
          />
          <button className="btn" onClick={load} disabled={loading}>
            {loading ? 'Refreshing…' : 'Refresh'}
          </button>
        </div>
      </div>

      {err && <div className="alert">{err}</div>}

      <div className="tableWrap">
        <table className="table">
          <thead>
            <tr>
              <th>Tag</th>
              <th>Customer</th>
              <th>Phone</th>
              <th>Status</th>
              <th>Caping</th>
              <th>Webbs</th>
              <th>Attempts</th>
              <th>Last Call</th>
              <th>Notes (new)</th>
              <th style={{width: 220}}>Actions</th>
            </tr>
          </thead>
          <tbody>
            {view.map(r => {
              const d = draft[r.tag] || { notes:'', busy:false, msg:'' };
              return (
                <tr key={r.tag}>
                  <td><Link href={`/intake?tag=${encodeURIComponent(r.tag)}`} className="lnk">{r.tag}</Link></td>
                  <td>{r.customer || ''}</td>
                  <td>{r.phone || ''}</td>
                  <td>{r.status || ''}</td>
                  <td>{r.capingStatus || ''}</td>
                  <td>{r.webbsStatus || ''}</td>
                  <td>{r.callAttempts ?? 0}</td>
                  <td className="muted">{r.lastCallAt || '—'}</td>
                  <td>
                    <input
                      value={d.notes}
                      onChange={(e) => setDraft(prev => ({ ...prev, [r.tag]: { ...(prev[r.tag]||{notes:''}), notes: e.target.value } }))}
                      placeholder={r.callNotes ? `Prev: ${r.callNotes.split('\n').slice(-1)[0]}` : 'Add note…'}
                    />
                  </td>
                  <td>
                    <div className="actions">
                      <button className="btn secondary" onClick={() => log(r.tag)} disabled={d.busy}>Log Attempt</button>
                      <button className="btn" onClick={() => mark(r.tag)} disabled={d.busy}>Mark Called</button>
                    </div>
                    <div className={`rowmsg ${d.msg ? 'show' : ''}`}>{d.msg || ''}</div>
                  </td>
                </tr>
              );
            })}
            {!loading && view.length === 0 && (
              <tr><td colSpan={10} style={{ textAlign:'center', padding:'20px' }}>Nothing to recall right now.</td></tr>
            )}
          </tbody>
        </table>
      </div>

      <style jsx>{`
        .head { display:flex; align-items:center; justify-content:space-between; gap:12px; margin: 10px 0 12px; }
        .tools { display:flex; gap:8px; align-items:center; }
        .search { min-width: 260px; }
        .alert { background:#fff7db; border:1px solid #fde68a; color:#92400e; padding:8px 10px; border-radius:8px; margin-bottom:8px; }

        .tableWrap { overflow:auto; border:1px solid #e5e7eb; border-radius: 10px; }
        .table { width: 100%; border-collapse: collapse; background: #fff; }
        .table th, .table td { border-bottom: 1px solid #e5e7eb; padding: 8px 10px; font-size: 14px; color: #0b0f12; }
        .table th { background: #f8fafc; text-align:left; }
        .table tr:hover td { background:#f2f5f7 !important; color:#0b0f12 !important; }

        .lnk { color:#1e40af; text-decoration: none; font-weight: 700; }
        .lnk:hover { text-decoration: underline; }

        input {
          border: 1px solid #cbd5e1; border-radius: 8px; padding: 6px 8px; background:#fff; color:#0b0f12;
          min-width: 200px;
        }
        .actions { display:flex; gap:6px; }
        .btn { background:#2f6f3f; color:#fff; border:1px solid transparent; border-radius:8px; padding:6px 10px; font-weight:800; cursor:pointer; }
        .btn:hover { background:#235532; }
        .btn.secondary { background:#f1f5f9; color:#0b0f12; border-color:#cbd5e1; }
        .btn.secondary:hover { background:#e2e8f0; }

        .rowmsg { height: 18px; font-size:12px; color:#065f46; margin-top:4px; opacity:0; transition:.15s ease; }
        .rowmsg.show { opacity: 1; }
      `}</style>
    </div>
  );
}

