// lib/api.ts
const BASE =
  process.env.NEXT_PUBLIC_GAS_BASE?.replace(/\/+$/,'') ||
  process.env.NEXT_PUBLIC_GAS_URL?.replace(/\/+$/,'') ||
  '/api/gas2';

export type GetJobResp = { ok: boolean; exists: boolean; job?: any; error?: string };
export type SaveResp   = { ok: boolean; error?: string; row?: number };
export type ProgressResp = { ok: boolean; nextStatus?: 'Processing'|'Finished'|null; error?: string };
export type SearchJobsResp = { ok: boolean; rows: any[]; total?: number; error?: string };
export type LogCallResp = { ok: boolean; attempts?: number; lastCallAt?: string; error?: string };
export type MarkCalledResp = { ok: boolean; scope?: 'auto'|'meat'|'cape'|'webbs'; error?: string };

async function fetchJson(url: string, init?: RequestInit, attempts = 2, timeoutMs = 8000) {
  let lastErr: any = null;
  for (let i = 0; i < attempts; i++) {
    const ctrl = new AbortController();
    const to = setTimeout(() => ctrl.abort(), timeoutMs);
    try {
      const res = await fetch(url, { cache: 'no-store', ...init, signal: ctrl.signal });
      clearTimeout(to);
      const data = await res.json().catch(() => ({}));
      if (res.ok) return data;
      lastErr = data?.error || `HTTP ${res.status}`;
      if ([429, 502, 503, 504].includes(res.status)) { await new Promise(r => setTimeout(r, 250*(i+1))); continue; }
      break;
    } catch (e:any) {
      clearTimeout(to);
      lastErr = e?.name === 'AbortError' ? 'Request timed out' : (e?.message || e);
      await new Promise(r => setTimeout(r, 250*(i+1)));
    }
  }
  return { ok: false, error: String(lastErr || 'Network error') };
}

export async function getJob(tag: string): Promise<GetJobResp> {
  return fetchJson(`${BASE}?action=get&tag=${encodeURIComponent(tag)}`);
}

export async function saveJob(job: any): Promise<SaveResp> {
  return fetchJson(`${BASE}`, {
    method: 'POST',
    headers: { 'Content-Type':'application/json' },
    body: JSON.stringify({ action:'save', job })
  });
}

export async function progress(tag: string): Promise<ProgressResp> {
  return fetchJson(`${BASE}`, {
    method: 'POST',
    headers: { 'Content-Type':'application/json' },
    body: JSON.stringify({ action:'progress', tag })
  });
}

export async function searchJobs(
  opts?: string | { q?: string; status?: string; limit?: number; offset?: number }
): Promise<SearchJobsResp> {
  const o = typeof opts === 'string' ? { q: opts } : (opts || {});
  const p = new URLSearchParams();
  if (o.q) p.set('q', o.q);
  if (o.status) p.set('status', o.status);
  if (o.limit != null) p.set('limit', String(o.limit));
  if (o.offset != null) p.set('offset', String(o.offset));
  const qs = p.toString();
  const url = qs ? `${BASE}?action=search&${qs}` : `${BASE}?action=search`;
  return fetchJson(url);
}

// NEW: record a call attempt (works for calls & recalls pages)
export async function logCall(args: {
  tag: string;
  scope?: 'auto'|'meat'|'cape'|'webbs';
  outcome?: string;
  notes?: string;
  by?: string;
}): Promise<LogCallResp> {
  return fetchJson(`${BASE}`, {
    method: 'POST',
    headers: { 'Content-Type':'application/json' },
    body: JSON.stringify({ action: 'logCall', ...args })
  });
}

// NEW: mark as “Called” with optional scope
export async function markCalled(args: {
  tag: string;
  scope?: 'auto'|'meat'|'cape'|'webbs';
  notes?: string;
  by?: string;
}): Promise<MarkCalledResp> {
  return fetchJson(`${BASE}`, {
    method: 'POST',
    headers: { 'Content-Type':'application/json' },
    body: JSON.stringify({ action: 'markCalled', ...args })
  });
}

// (optional) exported Job type for pages
export type Job = any;

